<template>
  <div class="course-detail">
    <!-- 课程标题和基本信息 -->
    <div class="course-header">
      <div class="course-title">
        <span class="title">{{ courseInfo.title }}</span>
      </div>
      <div class="course-meta">
        <span class="type">类型：{{ getCategoryName(courseInfo.categoryId) }}</span>
        <span class="time">课时：{{ courseInfo.periods || 0 }}课时</span>
        <span class="date">学习时间：{{ courseInfo.startTime || '-' }} - {{ courseInfo.endTime || '-' }}</span>
      </div>
    </div>

    <!-- 课程内容区域 -->
    <div class="course-content">
      <el-tabs v-model="activeTab">
        <el-tab-pane label="课程学习" name="study">
          <div class="content-wrapper">
            <!-- 左侧预览区域 -->
            <div class="preview-area">
              <file-viewer
                v-if="currentFile"
                :file="currentFile"
                @progress="handleProgress"
                @ended="handleVideoEnded"
              ></file-viewer>
              <div class="empty-tip" v-else>
                <el-empty description="请选择要预览的文件"></el-empty>
              </div>
            </div>

            <!-- 右侧目录 -->
            <div class="catalog-wrapper">
              <div class="catalog-header">
                <span class="title">课程目录</span>
              </div>
              <div class="catalog-list">
                <div class="section-item" 
                  v-for="(file, index) in courseInfo.courseFileList" 
                  :key="index"
                  @click="selectFile(file)"
                  :class="{ active: currentFile?.id === file.id }">
                  <div class="file-info">
                    <div class="file-name">
                      <span>{{ file.title || file.fileName }}</span>
                    </div>
                    <div class="file-meta">
                      <span class="duration">{{ formatDuration(file.duration) || '5分58秒' }}</span>
                      <!-- <div class="progress-wrapper">
                        <el-progress 
                          :percentage="file.progress || 0" 
                          :show-text="false"
                          :stroke-width="4"
                          class="progress-bar">
                        </el-progress>
                        <span class="progress-text">{{ file.progress || 0 }}%</span>
                      </div> -->
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="课程介绍" name="intro">
          <div class="intro-wrapper">
            <div class="intro-content" v-html="courseInfo.content || '暂无课程介绍'"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="课程问答(1)" name="qa">
          <!-- 课程问答内容 -->
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { getCourseDetail } from '@/api/course'
import { getImagesByIds } from '@/api/system'
import defaultCover from '@/assets/default-cover.jpg'
import FileViewer from '@/components/FileViewer.vue'
import logger from '@/utils/logger'

export default {
  name: 'CourseDetail',
  components: {
    FileViewer
  },
  data() {
    return {
      courseId: '',
      courseInfo: {},
      categoryName: '',
      activeTab: 'study',
      imageCache: new Map(),
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      currentSection: null,
      currentFile: null
    }
  },
  methods: {
    // 获取课程详情
    async fetchCourseDetail() {
      try {
        const res = await getCourseDetail(this.courseId)
        if (res.code === 200) {
          // 处理文件列表数据，添加防护性代码
          this.courseInfo = {
            ...res.data,
            // 保存分类名称
            categoryName: this.getCategoryName(res.data.categoryId),
            courseFileList: (res.data.courseFileList || [])
              .filter(file => file)
              .map(file => ({
                ...file,
                title: file?.title || file?.fileName || '未命名文件',
                duration: file?.duration || 0,
                progress: file?.progress || 0
              }))
          }
          
          // 加载课程封面
          if (this.courseInfo.coverUrl) {
            await this.loadImages(this.courseInfo.coverUrl)
          }

          // 如果有文件列表，默认选中第一个
          if (this.courseInfo.courseFileList?.length > 0) {
            this.currentFile = this.courseInfo.courseFileList[0]
          }

          logger.log('课程详情:', this.courseInfo)
        }
      } catch (error) {
        logger.error('获取课程详情失败:', error)
        this.$message.error('获取课程详情失败')
      }
    },

    // 加载图片
    async loadImages(imageId) {
      try {
        const res = await getImagesByIds(imageId)
        if (res.code === 200 && res.data) {
          res.data.forEach(item => {
            this.imageCache.set(item.ossId.toString(), item.url)
          })
        }
      } catch (error) {
        logger.error('获取图片失败:', error)
      }
    },

    // 获取封面URL
    getCoverUrl(course) {
      if (!course.coverUrl) return defaultCover
      return this.imageCache.get(course.coverUrl.toString()) || defaultCover
    },

    // 获取分类名称
    getCategoryName(categoryId) {
      // 优先使用 URL query 中的分类名称
      if (this.$route.query.categoryName) {
        return this.$route.query.categoryName
      }
      
      // 如果没有分类ID，返回默认值
      if (!categoryId) return '-'
      
      // 从父组件获取分类选项
      const category = this.$parent.categoryOptions?.find(
        item => item.value === categoryId.toString()
      )
      return category ? category.label : '-'
    },

    // 开始学习
    startStudy() {
      // TODO: 实现开始学习功能
      this.$message.success('开始学习')
    },

    // 收藏/取消收藏
    toggleCollect() {
      // TODO: 实现收藏功能
      this.courseInfo.collected = !this.courseInfo.collected
      this.$message.success(this.courseInfo.collected ? '收藏成功' : '已取消收藏')
    },

    // 处理目录点击
    handleNodeClick(data) {
      console.log(data)
      // TODO: 实现目录点击功能
    },

    // 格式化文件大小
    formatFileSize(size) {
      if (!size) return '0 B'
      const units = ['B', 'KB', 'MB', 'GB']
      let index = 0
      while (size >= 1024 && index < units.length - 1) {
        size /= 1024
        index++
      }
      return `${size.toFixed(2)} ${units[index]}`
    },

    // 下载文件
    downloadFile(file) {
      console.log('下载文件',file)
      if (!file.fileUrl) {
        this.$message.warning('文件链接不存在')
        return
      }
      
      // 创建一个隐藏的 a 标签来下载文件
      const link = document.createElement('a')
      link.href = file.fileUrl
      link.download = file.fileName
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    },

    // 播放视频
    playVideo(section) {
      this.currentSection = section
      // TODO: 实现视频播放功能
      console.log('播放视频:', section)
    },

    // 获取文件图标
    getFileIcon(fileType) {
      const iconMap = {
        video: 'el-icon-video-play',
        pdf: 'el-icon-document',
        doc: 'el-icon-document',
        ppt: 'el-icon-document',
        xls: 'el-icon-document'
      }
      return iconMap[fileType] || 'el-icon-document'
    },

    // 格式化时长
    formatDuration(seconds) {
      if (!seconds) return '0:00'
      const minutes = Math.floor(seconds / 60)
      const remainingSeconds = seconds % 60
      return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`
    },

    // 选择文件
    selectFile(file) {
      this.currentFile = file
      // 不需要滚动操作，因为预览区域已经固定在左侧
    },

    // 处理视频进度
    handleProgress(progress) {
      if (this.currentFile) {
        this.currentFile.progress = progress
        // TODO: 调用接口保存进度
      }
    },

    // 处理视频播放结束
    handleVideoEnded() {
      this.$message.success('视频播放完成')
      // 可以在这里自动播放下一个视频
      const currentIndex = this.courseInfo.courseFileList.findIndex(
        file => file.id === this.currentFile.id
      )
      if (currentIndex < this.courseInfo.courseFileList.length - 1) {
        this.selectFile(this.courseInfo.courseFileList[currentIndex + 1])
      }
    }
  },
  created() {
    this.courseId = this.$route.params.id
    // 从 query 中获取分类名称
    this.categoryName = this.$route.query.categoryName || '-'
    this.fetchCourseDetail()
  }
}
</script>

<style scoped>
