//import request from './request'
import request from '@/utils/request'

// 获取课程列表
export function getCourseList(params) {
  return request({
    url: '/courses/courses/list',
    method: 'get',
    params
  })
}

// 获取课程详情
export function getCourseDetail(id) {
  return request({
    url: `/courses/courses/${id}`,
    method: 'get'
  })
}

// 获取文件访问URL
export function getFileUrl(fileId) {
  return request({
    url: `/courses/file/${fileId}/url`,
    method: 'get'
  })
}

// 获取我的课程列表
export function getMyCourses(params) {
  return request({
    url: '/courses/collections/myCourses',
    method: 'get',
    params
  })
}

// 收藏课程
export function collectCourse(courseId) {
  return request({
    url: `/courses/collections/collect/${courseId}`,
    method: 'get'
  })
}

// 取消收藏
export function cancelCollectCourse(courseId) {
  return request({
    url: `/courses/collections/cancelCollect/${courseId}`,
    method: 'get'
  })
}