<template>
  <div class="video-player">
    <video
      ref="videoPlayer"
      class="video-element"
      controls
      :src="file.viewUrl"
      @timeupdate="handleTimeUpdate"
      @ended="handleEnded"
    >
      您的浏览器不支持 video 标签
    </video>
  </div>
</template>

<script>
export default {
  name: 'VideoPlayer',
  props: {
    file: {
      type: Object,
      required: true
    }
  },
  methods: {
    handleTimeUpdate(e) {
      const video = e.target
      const progress = Math.floor((video.currentTime / video.duration) * 100)
      this.$emit('progress', progress)
    },
    handleEnded() {
      this.$emit('ended')
    }
  }
}
</script>

<style scoped>
.video-player {
  width: 100%;
  height: 100%;
  background: #000;
}

.video-element {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
</style> 