<template>
  <div class="file-viewer">
    <!-- 视频播放器 -->
    <video-player
      v-if="isVideo"
      :file="file"
      @progress="handleProgress"
      @ended="handleEnded"
    ></video-player>

    <!-- Office 文件预览 -->
    <iframe
      v-else
      v-loading="loading"
      :src="getPreviewUrl"
      frameborder="0"
      width="100%"
      height="100%"
      @load="handleIframeLoad"
    ></iframe>
  </div>
</template>

<script>
import VideoPlayer from './VideoPlayer.vue'

export default {
  name: 'FileViewer',
  components: {
    VideoPlayer
  },
  props: {
    file: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: true
    }
  },
  computed: {
    isVideo() {
      // 检查文件类型或文件名后缀
      return this.file.fileType === 'video' || 
             this.file.fileName?.toLowerCase().endsWith('.mp4') ||
             this.file.fileName?.toLowerCase().endsWith('.webm')
    },
    isOfficeFile() {
      const officeTypes = ['doc', 'docx', 'xls', 'xlsx', 'ppt', 'pptx']
      // 检查文件类型或文件名后缀
      return officeTypes.includes(this.file.fileType) || 
             this.getFileExtension(this.file.fileName) in {
               'doc': true, 'docx': true,
               'xls': true, 'xlsx': true,
               'ppt': true, 'pptx': true
             }
    },
    getPreviewUrl() {
      if (!this.file.viewUrl) return ''
      
      // 调试信息
      console.log('文件信息:', {
        fileName: this.file.fileName,
        fileType: this.file.fileType,
        viewUrl: this.file.viewUrl,
        isPptFile: this.isPptFile,
        isOfficeFile: this.isOfficeFile
      })

      // 如果是 Office 文件，使用 Office Online Viewer
      if (this.isOfficeFile) {
        // 确保使用 HTTPS URL
        let secureUrl = this.file.viewUrl
        if (secureUrl.startsWith('http://')) {
          secureUrl = secureUrl.replace('http://', 'https://')
        }
        
        const encodedUrl = encodeURIComponent(secureUrl)
        // 对于 PPT 文件，使用 PowerPoint Online Viewer
        if (this.isPptFile) {
          const previewUrl = `https://view.officeapps.live.com/op/embed.aspx?src=${encodedUrl}&embedded=true`
          console.log('PPT预览URL:', previewUrl)
          return previewUrl
        }
        return `https://view.officeapps.live.com/op/embed.aspx?src=${encodedUrl}&embedded=true`
      }
      
      return this.file.viewUrl
    },
    isPptFile() {
      if (!this.file) return false
      
      // 检查文件名后缀
      const fileName = this.file.fileName?.toLowerCase() || ''
      const isPptByName = fileName.endsWith('.ppt') || fileName.endsWith('.pptx')
      
      // 检查文件类型
      const fileType = this.file.fileType?.toLowerCase() || ''
      const isPptByType = fileType === 'ppt' || fileType === 'pptx'
      
      const result = isPptByName || isPptByType
      console.log('PPT文件检查:', {
        fileName,
        fileType,
        isPptByName,
        isPptByType,
        result
      })
      
      return result
    }
  },
  watch: {
    file: {
      immediate: true,
      handler(newFile) {
        console.log('文件变更:', newFile)
        this.loading = true
      }
    }
  },
  methods: {
    getFileExtension(fileName) {
      if (!fileName) return ''
      const parts = fileName.toLowerCase().split('.')
      return parts.length > 1 ? parts[parts.length - 1] : ''
    },
    handleProgress(progress) {
      this.$emit('progress', progress)
    },
    handleEnded() {
      this.$emit('ended')
    },
    handleIframeLoad() {
      this.loading = false
      console.log('iframe加载完成:', {
        src: this.$el.querySelector('iframe')?.src,
        file: this.file.fileName
      })
    }
  }
}
</script>

<style scoped>
.file-viewer {
  width: 100%;
  height: 100%;
  background: #fff;
  position: relative;
  overflow: hidden;
}

iframe {
  background: #fff;
  border: none;
  width: 100%;
  height: 100%;
  display: block; /* 确保iframe正确显示 */
}

:deep(.el-loading-mask) {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 1000;
}
</style> 